const stateAwal = []

export default function rencanaimporReducer(state = stateAwal, action) {
    switch (action.type){
        case 'impor/changeRencana':{
            return [
                ...state,
                action.payload               
            ]
        }
        case 'impor/updateStatus':{
            var copyarray = [...state]
            var stateindex = copyarray.findIndex((obj) =>obj.id == action.payload)
            copyarray[stateindex].status = !copyarray[stateindex].status
            return [
                ...copyarray
            ]
        }
        default:
            return state
    }
}