const stateAwal = [
    {
        status:1,
        changedby:0,
        timestamp:Date().replace(/^\S+\s/,''),
        komentar:""
    }        
  ]

export default function historiReducer(state = stateAwal, action) {
    switch (action.type){
        case 'histori/updateHistori':{
            return action.payload
        }
        default:
            return state
    }
}