import { combineReducers } from 'redux'
import roleReducer from './features/roleSlice'
import stateReducer from './features/stateSlice'
import komoditasReducer from './features/komoditasReducer'
import rencanaimporReducer from './features/rencanaimporReducer'
import realisasiimporReducer from './features/realisasiimporReducer'
import rencanapenyerapanReducer from './features/rencanapenyerapanReducer'
import perusahaanpenyerapanReducer from './features/perusahaanpenyerapanReducer'
import produksiReducer from './features/produksiReducer'
import stokReducer from './features/stokReducer'
import perusahaandistribusiReducer from './features/perusahaandistribusiReducer'
import checkedReducer from './features/checkedReducer'
import checkReducer from './features/checkReducer'
import izinReducer from './features/izinReducer'
import piReducer from './features/piReducer'
import detailReducer from './features/detailReducer'
import statusReducer from './features/statusReducer'
import historiReducer from './features/historiReducer'
import rolemendagReducer from './features/rolemendagReducer'
import menuReducer from './features/menuReducer'

const rootReducer = combineReducers({
    userRole: roleReducer,
    changeState : stateReducer,
    komoditas : komoditasReducer,
    rencanaImpor : rencanaimporReducer,
    realisasiImpor : realisasiimporReducer,
    rencanaPenyerapan : rencanapenyerapanReducer,
    perusahaanPenyerapan : perusahaanpenyerapanReducer,
    produksi : produksiReducer,
    stok : stokReducer,
    perusahaanDistribusi : perusahaandistribusiReducer,
    checked : checkedReducer,
    check : checkReducer,
    izin:izinReducer,
    PI:piReducer,
    detail:detailReducer,
    status:statusReducer,
    histori:historiReducer,
    rolemendag:rolemendagReducer,
    menu:menuReducer
  })

export default rootReducer