const stateAwal = {
    komoditas : "garam"
}

export default function komoditasReducer(state = stateAwal, action) {
    switch (action.type){
        case 'komoditas/changeKomoditas':{
            return {
                ...state,
                komoditas : action.payload
            }
        }
        default:
            return state
    }
}